<template>
  <div class="index">
    <div class="displayFlex">
      <button @click="scrollToElement('location')">오시는길</button>
      <button @click="scrollToElement('contact')">연락하기</button>
      <button @click="scrollToElement('photo')">사진보기</button>
    </div>
  </div>
</template>

<script>
export default {
  name: 'IndexComponent',

  methods: {
    scrollToElement(id) {
      let targetElement;
      if (id == 'location') {
        targetElement = document.querySelector('#location');
      } else if (id == 'contact') {
        targetElement = document.querySelector('#contact');
      } else if (id == 'photo') {
        targetElement = document.querySelector('#photo');
      }
      
      if (targetElement) {
        targetElement.scrollIntoView({ behavior: 'smooth' }); // 선택한 요소로 스크롤합니다.
      }
    }
  }
}
</script>