<template>
  <div class="photo-box">
    <swiper class="swiper" @swiper="onSwiper" :options="swiperOptions">
      <swiper-button-prev class="swiper-button-prev" @click="prev"></swiper-button-prev>
      <swiper-button-next class="swiper-button-next" @click="next"></swiper-button-next>
      <swiper-slide>
        <img class="photo-image" src="@/assets/logo.png" alt="photo">
      </swiper-slide>

      <swiper-slide>
        <img class="photo-image" src="@/assets/logo.png" alt="photo">
      </swiper-slide>
    </swiper>
  </div>
</template>

<script>
import 'swiper/swiper-bundle.css';
import { Swiper, SwiperSlide } from 'swiper/vue';

export default {
  name: 'PhotoComponent',
  components: {
    Swiper,
    SwiperSlide,
  },
  data() {
    return {
      swiper: null,
      swiperOptions: {
        navigation: {
          nextEl: '.swiper-button-next',
          prevEl: '.swiper-button-prev',
        },
      },
    };
  },
  methods: {
    onSwiper(swiper) {
      this.swiper = swiper;
    },
    prev() {
      // 다음 슬라이드로 이동할 때 호출될 함수
      // personality 및 index에 따른 처리 로직을 추가하세요.
      this.swiper.slidePrev();
    },
    next() {
      // 다음 슬라이드로 이동할 때 호출될 함수
      // personality 및 index에 따른 처리 로직을 추가하세요.
      this.swiper.slideNext();
    },
  },
};
</script>
